/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #252525; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.header {
  border-bottom: 4px solid #e5493a; }

.header__wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 55px;
  padding-bottom: 55px; }

.header__logo {
  margin-right: 20px; }

.header__menu {
  margin: 0;
  padding: 0; }

@media (max-width: 768px) {
  .header__wrap {
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 25px;
    padding-bottom: 25px; }
  .header__logo {
    margin-right: 0;
    margin-bottom: 20px; } }

.menu {
  display: flex;
  list-style: none;
  flex-wrap: wrap; }

.menu__element {
  margin-right: 35px; }
  .menu__element:last-child {
    margin-right: 0; }

.menu__link {
  font-size: 12px;
  font-family: 'Raleway', sans-serif;
  color: #707070;
  text-transform: uppercase;
  line-height: 1.667;
  display: inline-block;
  text-decoration: none;
  transition-duration: 500ms;
  transition-property: color; }
  .menu__link::before {
    content: '';
    width: 8px;
    height: 2px;
    background-color: #e5493a;
    display: block;
    margin-bottom: 2px;
    opacity: 0;
    transition-duration: 500ms;
    transition-property: opacity; }
  .menu__link:hover {
    color: #e5493a; }
    .menu__link:hover::before {
      opacity: 1; }

@media (max-width: 768px) {
  .menu__element {
    margin-right: 20px; } }

.banner {
  background: url("../../img/bg-man.jpg") center top no-repeat;
  background-size: cover; }

.banner__wrap {
  padding-top: 255px;
  padding-bottom: 250px; }

.banner__info {
  max-width: 580px;
  margin-left: auto; }

.banner__title {
  font-size: 80px;
  font-family: 'Raleway', sans-serif;
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 1;
  margin: 0; }

.banner__little {
  display: block;
  font-size: 43px; }

.banner__red {
  color: #e5493a; }

.banner__text {
  font-size: 18px;
  font-family: 'Raleway', sans-serif;
  color: #555555;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 1.333;
  margin: 15px 0; }

.banner__button {
  border-radius: 5px;
  border: 2px solid transparent;
  background-color: #e5493a;
  width: 170px;
  height: 54px;
  font-size: 14px;
  font-family: 'Raleway', sans-serif;
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
  display: block;
  cursor: pointer;
  box-shadow: none;
  appearance: none;
  transition-duration: 500ms;
  transition-property: background-color, border-color;
  display: inline-block;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 25px; }
  .banner__button:hover, .banner__button:active {
    background-color: #ba2719;
    border-color: #ba2719; }

.banner__button--last {
  margin-right: 0;
  background-color: transparent;
  border-color: #fff; }

@media (max-width: 980px) {
  .banner__wrap {
    padding-top: 0;
    padding-bottom: 0; }
  .banner {
    background: none; }
  .banner__wrap {
    margin-top: 70px;
    margin-bottom: 50px; }
  .banner__info {
    max-width: 100%;
    margin-left: 0; } }

@media (max-width: 768px) {
  .banner__title {
    font-size: 50px; }
  .banner__little {
    font-size: 25px; }
  .banner__text {
    font-size: 16px; } }

@media (max-width: 480px) {
  .banner__title {
    font-size: 40px; }
  .banner__little {
    font-size: 20px; }
  .banner__text {
    font-size: 14px; }
  .banner__button {
    margin-top: 10px;
    margin-bottom: 10px; } }

.advantages__title {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.advantages__list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, auto));
  grid-column-gap: 30px;
  padding-top: 65px;
  padding-bottom: 50px; }

.advantages__item {
  margin: 15px 0; }
  .advantages__item:hover .advantages__img {
    border-color: #e5493a; }
  .advantages__item:hover .advantages__svg {
    fill: #e5493a; }
  .advantages__item:hover .advantages__subtitle {
    color: #e5493a; }
  .advantages__item:hover .advantages__text {
    color: #fff; }
    .advantages__item:hover .advantages__text::after {
      opacity: 1; }

.advantages__img {
  margin: 0 auto;
  border: 1px solid #363636;
  border-radius: 50%;
  position: relative;
  width: 104px;
  height: 104px;
  transition-duration: 500ms;
  transition-property: border; }

.advantages__svg {
  position: absolute;
  transform: translate(-50%, -50%);
  height: 40px;
  top: 50%;
  left: 50%;
  fill: #636363;
  transition-duration: 500ms;
  transition-property: fill; }

.advantages__subtitle {
  text-align: center;
  font-size: 18px;
  font-family: 'Raleway', sans-serif;
  color: #636363;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 1.111;
  margin: 20px 0;
  transition-duration: 500ms;
  transition-property: color; }

.advantages__text {
  font-size: 14px;
  font-family: 'Roboto', 'Arial', sans-serif;
  color: #555555;
  line-height: 1.429;
  margin: 20px 0;
  transition-duration: 500ms;
  transition-property: color; }
  .advantages__text::after {
    margin: 0 auto;
    display: block;
    content: "";
    width: 70px;
    height: 2px;
    background-color: #e5493a;
    margin-top: 25px;
    opacity: 0;
    transition-duration: 500ms;
    transition-property: background-color; }

@media (max-width: 480px) {
  .advantages__list {
    padding-top: 30px;
    padding-bottom: 25px; }
  .advantages__item {
    text-align: center; }
  .advantages__img {
    margin: 0 auto; } }

.works {
  background-color: #202020; }

.works__title-hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.works__list {
  padding-top: 80px;
  padding-bottom: 160px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(12, minmax(60px, auto));
  grid-auto-rows: minmax(270px, auto); }

.works__element--three {
  grid-column: span 3; }

.works__element--six {
  grid-column: span 6; }

.works__element--five {
  grid-column: span 5; }

.works__element--four {
  grid-column: span 4; }

.works__title {
  font-size: 30px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.1;
  margin: 25px 0;
  position: relative; }
  .works__title::before {
    background-color: #2f2f2f;
    position: absolute;
    width: 70px;
    height: 2px;
    left: 0;
    top: -25px;
    content: ''; }

.works__text {
  font-size: 14px;
  color: #464646;
  line-height: 1.429;
  margin: 20px 0; }

.works__element {
  position: relative; }
  .works__element:first-child {
    max-width: 270px; }
    .works__element:first-child::before {
      display: none; }
  .works__element::before {
    background-color: rgba(17, 17, 17, 0.85);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0;
    content: '';
    transition-property: opacity;
    transition-duration: 500ms; }
  .works__element:hover::before {
    z-index: 1;
    opacity: 1; }
  .works__element:hover .works__info {
    border-color: #e5493a;
    opacity: 1;
    z-index: 2; }
  .works__element:nth-child(2) {
    background: url("../../img/img1.jpg") center no-repeat;
    background-size: cover; }
  .works__element:nth-child(3) {
    background: url("../../img/img2.jpg") center no-repeat;
    background-size: cover; }
  .works__element:nth-child(4) {
    background: url("../../img/img3.jpg") center no-repeat;
    background-size: cover; }
  .works__element:nth-child(5) {
    background: url("../../img/img4.jpg") center no-repeat;
    background-size: cover; }
  .works__element:nth-child(6) {
    background: url("../../img/img5.jpg") center no-repeat;
    background-size: cover; }

.works__info {
  width: 100%;
  height: 100%;
  position: relative;
  border: 5px solid transparent;
  box-sizing: border-box;
  padding: 10px 25px 80px 25px;
  z-index: -1;
  opacity: 0;
  transition-property: opacity, border-color;
  transition-duration: 500ms; }

.works__name {
  font-size: 24px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.1;
  margin: 20px 0; }

.works__description {
  font-size: 12px;
  color: #959595;
  text-transform: uppercase;
  line-height: 1.667;
  margin: 20px 0; }
  .works__description::after {
    content: '';
    width: 70px;
    height: 2px;
    background-color: #e5493a;
    display: block;
    margin-top: 20px; }

.works__zoom, .works__link {
  border-radius: 50%;
  background-color: #252525;
  position: relative;
  cursor: pointer;
  display: inline-block;
  width: 42px;
  height: 42px;
  margin: 0 2px;
  appearance: none;
  padding: 0;
  border: none;
  transition-property: background-color;
  transition-duration: 500ms; }
  .works__zoom:hover, .works__zoom:active, .works__link:hover, .works__link:active {
    background-color: #e5493a; }
  .works__zoom svg, .works__link svg {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    fill: #fff;
    width: 15px;
    height: 15px; }

.works__btns {
  position: absolute;
  right: 30px;
  bottom: 30px; }
